<script lang="ts">
import { defineComponent } from "vue";
import BackButton from "@/UI/BackButton.vue";

export default defineComponent({
  setup() {
    return {};
  },
  data: () => ({
    name: "Card",
    nameNative: "Card",
    cards: "",
    cardsNat: "",
    dictPicPath: "/img/dict/card/",
    resJsonTxt: "",
    lang: "ru-RU",
    dUrl: "",
    dFile: "",
  }),
  mounted() {},
  methods: {
    loadLang() {
      //alert("done");
      // this.windowHeight = window.innerHeight;
      // this.windowWidth = window.innerWidth;
      // this.winDPR = window.devicePixelRatio;
      //console.log(this.voices)
      //console.log(voices);
      // speechSynthesis.onvoiceschanged = () => {
      //   voices = speechSynthesis.getVoices();
      //   this.voices = voices;
      //   alert("done");
      //   //console.log(this.voices);
      // };
    },
    save() {},
    async parse() {
      var newD: IDictionary = {
        name: this.name,
        Translations: [
          {
            name: this.nameNative,
            lang: "ru-RU",
          },
        ],
        items: [],
      };

      const nativeWords = this.cardsNat.split(/\r?\n/);
      var count = 0;
      newD.items = this.cards.split(/\r?\n/)      
      .map((item) => {
        return {
          id: count,
          name: item,
          image: this.dictPicPath + item.trim().replace(' ','').toLowerCase() + ".png",
          translates: [{ name: nativeWords[count++]??"", lang: "ru-RU" }],
        };
      })
      .filter(x=>x.name!='');

      //display in text
      this.resJsonTxt = JSON.stringify(newD, null, 2);

      const res = await fetch("https://ru.libretranslate.com/translate", {
	method: "POST",
	body: JSON.stringify({
		q: "test",
		source: "auto",
		target: "ru",
		format: "text",
		api_key: ""
	}),
	headers: { "Content-Type": "application/json" }
});

console.log(await res.json());

      const jsonData = encodeURIComponent(this.resJsonTxt);
      this.dUrl = `data:text/plain;charset=utf-8,${jsonData}`;
      this.dFile = this.name + "Cards.json";
    },
  },
  components: {
    BackButton,
  },
});
</script>

