<template>
  <div class="scene">
    <div
      class="flex flex-row justify-between items-center"
      style="height: 10vh"
    >
      <BackButton />
      <div class="flex flex-row cent">
        <h1 class="head--page">{{ currStory.name }}</h1>
        <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
        <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
      </div>
      <p></p>
    </div>
    <div
      class="flex flex-col antialiased justify-between items-center"
      style="min-height: 90vh"
    >
      <div
        v-if="isFirstLoad"
        class="flex flex-col overflow-auto justify-items-center items-center w-2/3"
        style="height: 70vh"
      >
        <p>
          {{ currStory.about }}
        </p>
        <img
          class="rounded-lg max-h-48 m-5"
          v-if="currStory.image"
          :src="currStory.image"
        />
        <div class="flex flex-row">
          <SoundButton
            :CaptionText="currStory.about ?? currStory.name"
            LinkSound=""
          />
          <SoundNativeButton
            v-if="currStory?.translations"
            :CaptionText="getNativeHead"
            LinkSound=""
          />
        </div>
        <button @click="startStory" class="btn--next mt-5">START</button>
      </div>

      <div
        v-else
        class="flex flex-col overflow-auto justify-items-center items-center w-2/3"
        style="height: 70vh; flex-direction: column-reverse"
      >
        <div class="w-full">
          <div
            class="flex flex-row gap-2.5 mt-5"
            v-for="item in currPosts"
            :key="item.id"
            :class="[
              item.isSent ? 'flex-row-reverse' : 'flex-row',
              item.actor ? '' : 'justify-center',
            ]"
          >
            <!-- <img class="w-8 h-8 rounded-full" src="/img/logo/me_big.png" /> -->
            <img
              class="w-8 h-8 rounded-full"
              v-if="item.actor"
              :src="getAvatar(item.actor)"
            />
            <div
              class="flex flex-col min-h-14 w-full max-w-[320px] leading-1.5 p-4 rounded-e-xl rounded-es-xl bg-gray-700"
            >
              <div
                v-if="item.actor"
                class="flex items-center space-x-2 rtl:space-x-reverse mb-2"
              >
                <span
                  class="text-sm font-semibold text-gray-900 dark:text-white"
                  >{{ item.actor }}</span
                >
                <span
                  class="text-sm font-normal text-gray-500 dark:text-gray-400"
                  >{{ item.time }}</span
                >
              </div>
              <p class="text-xl font-normal text-left py-2.5 text-white">
                {{ item.name }}
              </p>
              <div v-if="item.image" class="group relative my-2.5">
                <img
                  v-if="item.image"
                  :src="item.image"
                  class="rounded-lg w-20 h-20"
                />
              </div>
            </div>
            <div class="flex flex-col h-full align-middle items-center">
              <SoundButton :CaptionText="item.name" LinkSound="" />
              <SoundNativeButton
                class="m-5"
                v-if="currStory?.translations"
                :CaptionText="getNativeItem(item.translates)"
                LinkSound=""
                style="margin-top: 15px"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="flex m-5 flex-wrap" v-if="!isSolved">
        <CardWordSel
          v-for="item in currPAnswers"
          :IsEnabled="!isSuspended"
          :key="item.id"
          :Caption="item.name"
          :IsCorrect="item.isCorrect"
          @wordClick="cardClick(item)"
        />
      </div>
      <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
      <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
      <!-- <h2 v-if="isSolved">Good JOB</h2>
      <h2 v-else>Waiting for solve...</h2> -->
      <button
        v-if="
          !isFirstLoad &&
          !isSolved &&
          !isSuspended &&
          !(currPAnswers.length > 0)
        "
        type="submit"
        @click="nextClick"
        :disabled="isSolved"
        class="btn--next"
      >
        NEXT
      </button>
      <button
        v-if="isSolved"
        type="submit"
        @click="restartStory"
        class="btn--restart"
      >
        RESTART
      </button>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from "vue";
import { useSound } from "@vueuse/sound";

// import mixin
import mxMacro from "@/mixins/myMacro";
import CardWordSel from "@/components/cards/CardItemSelWord.vue";

import BackButton from "@/UI/BackButton.vue";
import SoundButton from "@/UI/SoundButton.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";

export default defineComponent({
  mixins: [mxMacro],
  setup() {
    const wrongSound = useSound("/sound/common/wrong3.waw", { volume: 1 });
    const goodSound = useSound("sound/common/success2.waw", { volume: 1 });
    const audioW = ref<HTMLAudioElement>();
    const audioS = ref<HTMLAudioElement>();
    const aInput = ref<HTMLInputElement>();

    return { wrongSound, goodSound, audioW, audioS, aInput };
  },
  data() {
    return {
      currStory: {} as IDialog,
      currPosts: [] as IDialogItem[],
      currPAnswers: [] as IDialogItemAnswer[],

      currObj: {} as IDictionaryItem,
      isSuspended: false,
      count: 0,
      isSolved: false,
      isFirstLoad: true,
    };
  },
  computed: {
    getNativeHead() {
      var locLang = "ru-RU";
      if (!this.currStory) return "";

      var translates = <ITranslations[]>this.currStory?.translations;

      if (translates) {
        var foundTransl = translates.filter((a) => a.lang === locLang);
        console.log(foundTransl[0]?.name);
        return foundTransl[0]?.name;
      } else {
        return "";
      }
    },
    getNative() {
      var locLang = "ru-RU";
      // console.log(this.currObj as IDictionaryItem);

      var translates = <ITranslations[]>this.currObj.translates;

      if (this.currObj?.translates) {
        //console.log(this.currObj.translates);
        var castedArr = <ITranslations[]>this.currObj.translates;
        //console.log(castedArr);
        var foundTransl = castedArr.filter((a) => a.lang === locLang);
        //console.log(foundTransl[0]);
        return foundTransl[0]?.name;
      } else {
        return "";
      }
    },
  },
  async created() {
    import("@/data/dialog/" + this.$route.params.storyName + ".json").then(
      (module) => {
        this.currStory = module.default;

        //this.moveNext();
      }
    );
  },
  components: {
    BackButton,
    SoundButton,
    SoundNativeButton,
    CardWordSel,
  },
  methods: {
    startStory() {
      this.isFirstLoad = false;
      this.moveNext();
    },
    cardClick(answer: IDialogItemAnswer) {
      if (!answer.isCorrect || this.isSuspended) return;
      this.isSuspended = true;

      var currentDate = new Date();
      var newPost: IDialogItem = {
        id: 0,
        name: answer.name,
        nameA: null,
        actor: "$ME",
        isSent: true,
        image: null,
        bg: null,
        translates: [],
        answers: [],
        time: currentDate.getHours() + ":" + currentDate.getMinutes(),
      };

      newPost.actor = this.replaceMacro(newPost.actor ?? "");
      newPost.name = this.replaceMacro(newPost.name ?? "");

      this.currPosts.push(newPost);
      setTimeout(() => {
        this.moveNext();
        this.isSuspended = false;
      }, 2000);
    },
    nextClick() {
      this.moveNext();
      this.isSuspended = true;
      //setTimeout(() => this.moveNext(), 2000);
      setTimeout(() => (this.isSuspended = false), 2000);
    },
    restartStory() {
      this.count = 0;
      this.currPosts = [];
      this.isSolved = false;
      this.moveNext();
    },
    getAvatar(actorName: string | null) {
      if (actorName) {
        var av = this.currStory.actors.filter((a) => a.name === actorName)[0]
          ?.image;
        if (av) {
          return av;
        } else {
          return "/img/logo/me_big.png";
        }
      } else {
        return "/img/logo/me_big.png";
      }
    },
    moveNext() {
      if (this.currStory.Items.length > this.count) {
        var currentDate = new Date();
        this.currStory.Items[this.count].actor = this.replaceMacro(
          this.currStory.Items[this.count].actor ?? ""
        );
        this.currStory.Items[this.count].name = this.replaceMacro(
          this.currStory.Items[this.count].name ?? ""
        );

        this.currStory.Items[this.count].time =
          currentDate.getHours() + ":" + currentDate.getMinutes();
        this.currPosts.push(this.currStory.Items[this.count]);

        if (
          this.currStory.Items[this.count]?.answers &&
          this.currStory.Items[this.count].answers.length > 0
        ) {
          this.currPAnswers = this.currStory.Items[this.count].answers
            .sort(function () {
              return 0.7 - Math.random();
            })
            .map((item) => {
              return {
                id: Math.floor(Math.random() * 100000),
                name: this.replaceMacro(item.name),
                isCorrect: item.isCorrect,
              };
            });
        } else {
          this.currPAnswers = [];
        }

        this.count++;
      } else {
        this.isSolved = true;
      }
    },
    getNativeItem(transl: ITranslations[]) {
      var locLang = localStorage.lang;
      // console.log(this.currObj as IDictionaryItem);

      var translates = transl;

      if (translates && translates.length > 0) {
        var foundTransl = translates.filter((a) => a.lang === locLang);

        //console.log(foundTransl[0]);
        return this.replaceMacro(foundTransl[0]?.name);
      } else {
        return "";
      }

      return "";
    },
  },
});
</script>

<style scoped>
.scene {
  /* display: flex;
  height: 100vh; */
  background: rgb(238, 174, 202);
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  /* background-color: gray; */
}
</style>
