<template>
  <div class="flex flex-row">
    <div
      @click="select"
      class="card--sm--word min-w-14  items-center p-1"
      :class="{
        // 'card--correct': IsCorrect && isSelected,
        'card--dimmed': isDone,
        //'card--wrong': !IsCorrect && isSelected,
        shake: !IsCorrect && isClicked,
        pulse: isDone,
      }"
    >
      <h2 class="text-7xl self-center">{{ Answ ?? Caption }}</h2>

      <!-- <p>IMG:{{ Img }}</p> -->
      <!-- <img :src="Img"/> -->
    </div>
    <SoundButtonSQ @click="makeSound">

    </SoundButtonSQ>
    <VueSpeechSynthesis
      ref="audioOut"
      :text="Answ ?? Caption"
      lang="en-US"
      :pitch="1"
      :volume="1"
      :rate="0.7"
      :read-on-load="false"
      :read-on-change="false"
    />
    <audio hidden="true" ref="audioW" src="/sound/common/wrong2.wav" />
    <audio hidden="true" ref="audioS" src="/sound/common/success.wav" />
  </div>
</template>

<script lang="ts">
import SoundButtonSQ from "@/UI/SoundButtonSQ.vue";
import SoundNativeButton from "@/UI/SoundNativeButton.vue";
import { defineComponent, ref, onMounted } from "vue";
import VueSpeechSynthesis from "@/components/VueSpeechSynthesis.vue";

const audioOut = ref(VueSpeechSynthesis);

export default defineComponent({
  setup() {
    const audioW = ref<HTMLAudioElement>();
    const audioS = ref<HTMLAudioElement>();
    return { audioW, audioS };
  },
  data() {
    return {
      nativeCaption: "",
      isDone: false,
      isClicked: false,
    };
  },
  methods: {
    makeSound() {
       

      (this.$refs.audioOut as typeof VueSpeechSynthesis).start();
    },
    select() {
      if (this.isDone) return;
      if (!this.IsEnabled) return;
      if (this.IsCorrect) {
        //setTimeout(() => this.audioS?.play(), 10);
        //this.audioS?.play();
        setTimeout(() => (this.isDone = true), 100);
        this.$emit('wordClick', this.Answ ?? this.Caption)
      } else {
        this.isClicked = true;
        setTimeout(() => (this.isClicked = false), 200);

        //this.audioW?.play();
      }

      //(this.$refs.audioOut as typeof VueSpeechSynthesis).start();
    },
  },

  computed: {
    getNative() {
      //console.log("getNative");
      var locLang = "ru-RU";
      if (this.Translations) {
        //console.log(this.Translations);
        var castedArr = <ITranslations[]>this.Translations;
        // console.log(castedArr);
        var foundTransl = castedArr.filter((a) => a.lang == locLang);
        // console.log(foundTransl[0].name);

        return foundTransl[0].name;
      } else {
        return "";
      }
    },
  },
  props: {
    Caption: {
      type: String,
      required: true,
    },
    Translations: {
      type: Array,
      required: false,
    },

    BGColor: {
      type: String,
      required: false,
    },
    Img: {
      type: String,
      required: false,
    },
    IsCorrect: {
      type: Boolean,
      required: false,
      default: false,
    },
    IsEnabled: {
      type: Boolean,
      required: false,
      default: false,
    },
    Answ: {
      type: String,
      required: false,
    },
  },
  components: { SoundButtonSQ, SoundNativeButton, VueSpeechSynthesis },
});
</script>

<style scoped>
.card--sm--word {
  /* border: 2px violet solid; */
  /* transform: translateY(-5px); */
  /* filter: drop-shadow(15px 15px 15px #a809cc); */
  background-color: white;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: end;
  /* width: 200px;
  height: 200px; */
  height: 40px;
  margin-left: 5px;
}
.card--correct {
  border: 5px rgb(11, 182, 76) solid;
  filter: drop-shadow(15px 15px 15px rgb(11, 182, 76));
}
.card--wrong {
  border: 5px rgb(182, 11, 11) solid;
  filter: drop-shadow(15px 15px 15px rgb(182, 11, 11));
  opacity: 0.2;
}

.card--dimmed {
  opacity: 0.3;
}

.card--sm--img img {
  object-fit: cover;
  object-position: center;
}
h2 {
  @apply text-3xl font-bold;
}

@keyframes min-round_round {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(-360deg);
  }
}
</style>
