var myMacro = {
     
    methods: {
      replaceMacro: function (str:string) {
        
        var nameEn=localStorage.name;        
        var nameNat=localStorage.nameNative;
        var age=localStorage.getItem("age");
        var sex=localStorage.getItem("sex");
        var lang=localStorage.getItem("lang");
        
        var newString=str.replace('$ME',nameEn??'NAME');
        newString=newString.replace('$MN',nameNat??'NAME');
        newString=newString.replace('$AGE',age??'AGE');
        newString=newString.replace('$SEX',sex??'SEX');

        return newString;
      }
    }
  }


  export default myMacro