import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import HomeView from "../views/HomeView.vue";
import HelloView from "../views/HelloView.vue";
import DictionarySelPic from "@/components/engines/DictionarySelectPicEngine.vue";
import DictionarySelCloudPic from "@/components/engines/DictionarySelectPicCloudEngine.vue";
import DictionarySelectPicWheelEngine from "@/components/engines/DictionarySelectPicWheelEngine.vue";
import SimpleDictionary from "@/components/engines/DictionarySimpleEngine.vue";
import WriteDictionary from "@/components/engines/DictionaryWritingEngine.vue";
import WriteSelDictionary from "@/components/engines/DictionaryWritingSelEngine.vue";
import StoryEngine from "@/components/engines/DictionaryStoryEngine.vue";
import DialogEngine from "@/components/engines/DictionaryDialogEngine.vue";
import ColorView from "@/views/ColorView.vue";
import Settings from "@/views/SettingsView.vue";
import Profile from "@/views/ProfileView.vue";
import CardCreate from "@/views/CardCreateView.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/profile",
    name: "profile",
    component: Profile,
  },
  {
    path: "/create",
    name: "create",
    component: CardCreate,
  },
  {
    path: "/hello",
    name: "hello",
    component: HelloView,
  },
  {
    path: "/ColorView",
    name: "ColorView",
    component: ColorView,
  },
  {
    path: "/dictSPic/:dictname",
    name: "dictSPic",
    component: DictionarySelPic,
  },
  {
    path: "/settings",
    name: "settings",
    component: Settings,
  },

  {
    path: "/simpleDict/:dictname+",
    name: "simpleDict",

    component: SimpleDictionary,
  },
  {
    path: "/dictSCloudPic/:dictname",
    name: "dictSCloudPic",

    component: DictionarySelCloudPic,
  },
  {
    path: "/dictSWheelPic/:dictname",
    name: "dictSWheelPic",

    component: DictionarySelectPicWheelEngine,
  },
  {
    path: "/dictWrite/:dictname",
    name: "dictWrite",

    component: WriteDictionary,
  },
  {
    path: "/dictSWrite/:dictname",
    name: "dictSWrite",

    component: WriteSelDictionary,
  },

  {
    path: "/storySimple/:storyName",
    name: "storySimple",

    component: StoryEngine,
  },
  {
    path: "/dialogSimple/:storyName",
    name: "dialogSimple",

    component: DialogEngine,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
